.dashboard-table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

.dashboard-td,
.dashboard-th {
  border: 1px solid #dddddd;
  text-align: left;
  padding: 8px;
}

.dashboard-tr:nth-child(even) {
  background-color: rgba(0, 0, 0, 0.1);
}



